.PolicyBody{
    background-color: var(--clrtemplatebg);
    width: 100%;
    display: flex;
    justify-content: center;
    padding-top: 100px;
    padding-bottom: 70px;
}
.PolicyContainer{
    width: 80%;
    background-color: var(--clrdivcolor);
    padding: 40px 80px;
    border-radius: 15px;
    max-width: 1520px;
}
.PolicyContainer>h1{
    margin-bottom: 40px;
    font-size: 40px;
    text-align: center;
    padding-bottom: 15px;
    color: var(--clrdark);
    border-bottom: 3px solid var(--clrlighter);
}
.PolicyContainer>ul{
    list-style-type:disc;
    margin-left: 40px;
}

.PolicyContainer>p{
    font-size:18px; 
    color: var(--clrdark);
    margin-bottom: 20px;
}
.PolicyContainer>ul{
    color: var(--clrdark);
    font-size:18px;
    margin: 20px 40px;
}
.PolicyContainer>ul > li{
    margin-bottom: 8px;
}
.PolicyContainer>h3{
    color: var(--clrdark);
    text-decoration: underline;
    font-size: 22px;
    margin: 30px 0 15px 0;
}

@media (width < 950px){
.PolicyContainer{
    padding: 40px 50px;
}
.PolicyContainer>p{
    font-size:16px; 
}
.PolicyContainer>ul{
    color: var(--clrdark);
    font-size:15px;
    margin: 20px 20px;
}
.PolicyContainer>ul > li{
    margin-bottom: 8px;
}
.PolicyContainer>h3{
    color: var(--clrdark);
    text-decoration: underline;
    font-size: 18px;
    margin: 30px 0 15px 0;
}
.PolicyContainer>h1{
    color: var(--clrdark);
    font-size: 28px;
    border-bottom: 2px solid var(--clrlighter);

}
}

@media (width < 520px){
    .PolicyContainer>p{
        font-size:15px; 
    }
    
    .PolicyContainer{
        width: 90%;
        padding: 40px 30px;
    }
    
}