.postBoxContainer {
    display: flex;
    max-width: 1520px;
    margin-bottom: 40px;
    width: 100%;
    margin-top: 10px;
    /* flex-direction: column; */
    align-items: flex-start;
    /* background-color: var(--clrdivcolor); */
    color: var(--clrdark);
    margin-right: 10px;
}

.postBoxContainer .postUserImg {
    /* padding-top: 5px; */
    width: 8%;
    display: flex;
    justify-content: center;
    align-items: center;
}

.postBoxContainer .postUserImg img {
    height: 60px;
    width: 60px;
    border-radius: 50px;
    margin-right: 10px;
}

.postBoxContent {
    /* box-shadow: 0 5px 5px rgb(0 0 0 / 5%); */
    /* box-shadow: 2px 2px 15px 5px rgb(226 226 226 / 100%); */
    border-radius: 5px;
    display: flex;
    margin: 10px 0;
    align-items: center;
    background-color: var(--clrdivcolor);
    height: auto;
    width: 100%;
    padding: 0 20px 0 30px;
}

.nameAndDate {
    display: flex;
    /* padding: 0px 0px 15px 10px; */
    height: 30px;
    padding-top: 8px;
    margin-bottom: 10px;
    width: 100%;
}

.postedByName {
    font-weight: bold;
    color: var(--clrdark);
    font-size: 20px;
    margin-right: 30px;
}

.postedByName:hover {
    border-bottom: 1px solid var(--clrdark);
}

.postedByName>i,
.postedDate>i {
    margin-right: 5px;
    color: var(--clrlight);
    font-size: 12px;
}

.postedDate {
    font-size: 15px;
    margin-top: 3px;
    font-weight: normal;
    color: var(--clrlight);
}

.postBoxDesc {
    padding-right: 20px;
    font-size: 18px;
    line-height: 30px;
    margin-bottom: 30px;
    height: auto;
    width: 100%;
    padding: 0 30px;
    margin-right: 20px;
    
}

.postBoxDesc > iframe{
    width: 60%;
    display: block;
    /* margin: 0 auto; */
    margin-top: 10px;
}
.wrapperDiv > .RepliesNIcon{
    margin-right: 0;
}
.nameAndDescWrapper{
    background-color: var(--clrdivcolor);
    width: 100%;
}
.postBoxDesc p{
    height: auto;
    overflow-wrap: break-word;
}

.postBoxDesc img {
    width: auto;
    max-width: 100%;
    height: auto;
}

.Title {
    height: auto;
    margin-bottom: 25px;
    margin-top: 2px;
    padding-bottom: 10px;
    border-bottom: 2px solid var(--clrlighter);
}

.Title>h2 {
    font-size: 32px;
}

.wrapperDiv {
    /* margin-top: 20px; */
    display: flex;
    border-bottom: 1px solid var(--clrtemplatebg);
    height: auto;
    padding-bottom: 5px;
    width: 100%;
    margin-bottom: 5px;
    justify-content: space-between;
}


@media (width < 1050px) {
    .postBoxContainer > .postUserImg{
        width: 50px;
        display: flex;
        justify-content: center;
        margin: 0 10px;
    }
    .postBoxContainer > .postUserImg > img{
        width: 50px;
        height: 50px;
        margin-right: 0;
    }
    .nameAndDate{
        padding-top: 5px;
    }
    .wrapperDiv > .RepliesNIcon{
        width: 20px;
    }
    .postBoxDesc{
        font-size: 15px;
    }
    .postBoxDesc{
        padding: 0 25px;
    }
    .postBoxContent{
        padding-left: 20px;
    }
    .postBoxDesc > iframe{
        width: 90%;
        margin: 0 auto;
    }
}

@media (width < 650px) {

    .postBoxDesc > iframe{
        margin: 0 auto;
        width: 100%;
    }
    .postBoxContainer{
        width: 100%;
    }
    .postBoxContainer > .postUserImg{
        margin-top: 5px;
        padding-right: 0;
    }
    .postBoxContainer > .postUserImg > img{
        width: 40px;
        height: 40px;
        margin-right: 0;
    }
    .wrapperDiv .RepliesNIcon{
        width: 100px;
    }
    .wrapperDiv .RepliesNIcon .dropdown{
        right: -30px;
        top: 40px;
    }
    .wrapperDiv .RepliesNIcon .dropdown .sub-item{
        font-size: 12px;
        padding: 5px 8px 5px 5px;
    }
    .commentDelete {
        position: relative;
    }
    

     .postedByName{
        font-size: 17px;
        margin-left: 5px;
        margin-top: 3px;
    }
}