.Footer {
  width: 100%;
  bottom: 0;
  padding: 25px 0;
  /* height: 160px; */
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
  background-color: var(--clrfootercolor);
}
.FooterContainer {
  max-width: 1520px;
  margin-top: 20px;
  display: flex;
  justify-content: space-between;
  width: 82%;
  color: var(--clrdark);
  display: flex;
}
.FooterText {
  width: 50%;
  padding-right: 50px;
}
.Footer h2 {
  font-size: 20px;
  text-align: start;
  margin-bottom: 15px;
}
.FooterText p {
  font-size: 14px;
  font-weight: normal;
  color: grey;
  padding-right: 40px;
}
.FooterLinks {
  width: 15%;

}
.FooterLinks h5 {
  color: var(--clrlight);
  font-weight: normal;
  font-size: 15px;
  width: auto;
  padding-bottom: 10px;
  transition: 0.3s;
}
.FooterLinks h5:hover {
  color: var(--clrprimary);
}
.FooterLinks h5:hover {
  cursor: pointer;
}
.copyright {
  margin-top: 20px;
}
.copyright p {
  color: grey;
  font-size: 18px;
}

@media (width < 768px) {
  .FooterText{
    width: 90%;
    padding: 0;
  }
  .FooterText > p {
    font-size: 13px;
  }
  .FooterLinks{
    margin-right: 10px;
    margin-top: 35px;
    width: 25%;
  }
  .FooterLinks > h5 {
    font-size: 13px;

  }
  .flright{
    margin-left: 20px;
  }
.copyright p {
  font-size: 13px;
}
}