nav {
  display: flex;
  z-index: 1000;
  width: 100%;
  top: 0;
  justify-content: center;
  position: fixed;
  flex-direction: column;
}
.NavbarContainer {
  height: 75px;
  background-color: var(--clrdivcolor);
  border-bottom: 3px solid var(--clrnavbarborder);
  display: flex;
  justify-content: center;
  width: 100%;
}
.Navbar {
  width: 90%;
  max-width: 1520px;
  color: var(--clrprimary);
  display: flex;
  justify-content: space-between;
}
.SearchBar {
  height: 32px;
  max-width: 1520px;
  overflow: hidden;
  animation: fadein 0.2s;
  display: flex;
  justify-content: center;
  width: 90%;
  text-align: start;
  border-radius: 20px;
  color: var(--clrdark);
  /* background-color: rgb(236, 235, 235); */
  /* background-color: rgb(244, 242, 242); */
  background-color: var(--clrdivcolor);


  margin: 8px auto 0;
}

.searchIcon {
  color: var(--clrtext_primary);
  padding: 7px 4px 0px 15px;
  font-size: 13px;
  background: none;
}
.SearchBar input {
  border-radius: 20px;
  width: 95%;
  border: none;
  font-size: 15px;
  color: var(--clrdark);
  outline: none;
  /* background-color: rgb(236, 235, 235); */
  /* background-color: rgb(244, 242, 242); */
  background-color: var(--clrdivcolor);


  padding: 13px 20px 15px 5px;
}
.brandAndLinks {
  /* width: 60%; */
  display: flex;
}

.navBranding {
  margin-top: 15px;
  width: 180px;
  margin-right: 10px;
  color: var(--clrprimary);
  display: flex;
  font-size: 26px;
}

.navLogo {
  display: flex;
  flex-direction: row;
  position: absolute;
}

.faRed {
  color: var(--clrprimary);
}

.fa2 {
  position: inherit;
  color: var(--clrdark);
  margin-top: 11px;
}

.fa3 {
  position: inherit;
  margin-top: 22px;
}

.navTitle {
  margin-top: 7px;
  font-weight: bold;
  margin-left: 10px;
  cursor: pointer;
  color: var(--clrprimary);
  text-decoration: none;
}
.bgNone:hover {
  background: none;
  color: var(--clrdark);
}
a {
  text-decoration: none;
}

.navLinks {
  display: flex;
  align-items: center;
  list-style-type: none;
  justify-content: space-between;
  color: var(--clrdark);

}

.navLinks li {
  font-weight: bold;
  transition: 0.2s;
  font-size: 18px;
  letter-spacing: 0.5px;
  margin: 15px 0 18px 2px;
  background-color: var(--clrdivcolor);
  color: var(--clrdark);

  cursor: pointer;
}

.navLinks li a {
  transition: 0.3s ease-out;
  padding: 10px 20px;
  text-decoration: none;
  color: var(--clrdark);
  background-color: var(--clrdivcolor);
}

.navLinks .active {
  border-radius: 7px;
  /* background-color: var(--clrprimarylight); */
  background-color: var(--clrnavlinkactive);
  color: var(--clrprimary);
}

.searchBtn {
  border: none;
  outline: none;
  padding: 10px;
  font-size: 15px;
  cursor: pointer;
  background: none;
  color: var(--clrprimary);
}
.searchBtn i {
  transition: 0.5s;
  color: var(--clrdark);
}
.searchBtn:hover > i {
  color: var(--clrprimary);
  transition: 0.2s;
}
.navBtns {
  display: flex;
  justify-content: center;
  align-items: center;
}

.loginBtn {
  color: var(--clrdark);
  transition: 0.3s ease-out;
  cursor: pointer;
  border: none;
  font-weight: 900;
  font-size: 18px;
  margin-right: 25px;
  box-shadow: 0;
  background: none;
}

.navLinks li:hover,
.navLinks a:hover {
  /* background-color: #fff1f0; */
  background-color: var(--clrnavlinkactive);

  transition: 0.3s;
  border-radius: 7px;
  color: var(--clrprimary);
}

.loginBtn:hover {
  color: var(--clrprimary);
  transition: 0.3s;
}

.authBtnOuters{
  margin-left: 4%;
}

/* Style for the hamburger icon */
.hamburger {
  display: none;
  cursor: pointer;
  margin-top: auto;
  margin-bottom: auto;
  order: 2;
  justify-self: flex-end;
  margin-left: 20px;
  height: 14px;
  /* z-index: 10; */
}

/* Style for the hamburger stripes */
.hamburger span {
  display: block;
  width: 25px;
  height: 3px;
  background-color: var(--clrdark);
  margin-bottom: 5px;
  position: relative;
  border-radius: 2px;
  z-index: 1;
  transform-origin: 0 0;
  transition: all 0.5s cubic-bezier(0.75, 0, 0.125, 1);
}

.profile{
  height: 100%;
  display: flex;
  justify-content: flex-end;
  align-items: center;
}

.profile>img {
  height: 48px;
  width: 48px;
  border-radius: 25px;
  background-size: cover;
  background-repeat: no-repeat;
  background-position: top center;
  cursor: pointer;
}

.sub-item {
  width: 100%;
  display: flex;
  align-items: center;
  transition: 0.2s;
  border-radius: 10px;
  color: var(--clrdark);
  gap: 8px;
  margin-bottom: 5px;
  cursor: pointer;
  padding: 10px ;
}

.sub-item:hover {
  background-color: var(--clrdropdownhover);
}

.dropdown{
  position: absolute;
  top: 65px;
  padding: 10px 10px 5px 10px;
  overflow: hidden;
  width: 200px;
  border-radius: 10px;
  /* right: 5%; */
  animation: fadein 0.4s;
  box-shadow: rgb(0 0 0 / 25%) 0px 2px 5px;
  z-index: 100;
  background-color: var(--clrdivcolor);
}

.sub-item>i{
  padding-top: 2px;
  width: 20px;
}


.PanelBtnNProfile{
  display: flex;
  align-items: center;
  /* background-color: green; */
}

.SearchBar > input:focus{
  border: none;
}

.Searches {
  max-width: 1520px;
  overflow: hidden;
  animation: fadein 0.2s;
  width: 90%;
  text-align: start;
  background-color: var(--clrdivcolor);
  margin: 10px auto 0;
  border-radius: 5px;
  box-shadow: 0 2px 6px rgba(0,0,0,0.35);
}

.Searches a:hover{
  background-color: var(--clrdropdownhover);
}

.Searches a{
  border-bottom: 1px solid var(--clrtemplatebg);
  display: block;
  color: var(--clrdark);
  padding: 12px 15px;
}




.darkModeDiv{
  margin-right: 20px;
  width: 100px;
  height: 50px;
  display: flex;
  align-items: center;
}

.darkModeDiv .fa-sun {
  color: gold;
  margin-right: 8px;
  transition: 0.8s;
}
.darkModeDiv .fa-moon {
  color: var(--clrdark);
  transition: 0.8s;
  margin-left: 8px;
}

.switch {
  position: relative;
  display: inline-block;
  width: 40px;
  height: 20px;
}

/* Hide default HTML checkbox */
.switch input {
  opacity: 0;
  width: 0;
  height: 0;
}

/* The slider */
.slider {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  /* background-color: #ccc; */
  -webkit-transition: .4s;
  transition: .4s;
}

.slider:before {
  position: absolute;
  content: "";
  height: 20px;
  width: 20px;
  /* left: 4px; */
  /* bottom: 4px; */
  background-color: var(--clrlighter);
  -webkit-transition: .4s;
  transition: .4s;
}

.darkmodeBtn:checked + .slider {
  /* background-color: var(--clrbackground); */
  background-color: var(--clrtemplatebg);
}


.darkmodeBtn:checked + .slider:before {
  -webkit-transform: translateX(20px);
  -ms-transform: translateX(26px);
  transform: translateX(20px);
}

/* Rounded sliders */
.slider.round {
  border-radius: 34px;
}

.slider.round:before {
  border-radius: 50%;
}



@media (width < 950px){
  
  .hamburger{
    display: inline-block;
  }
  .navBtns > .Btn{
    font-size: 13px;
    margin-top: 2px;
    height: 30px;
  }
  .navLinks{
    position: fixed;
    top: 0px;
    left: 0;
    width: 100%;
    /* height: 100vh; */
    /* background-color: #fff; */
    background-color: var(--clrdivcolor);
    z-index: -2;
    transform: translateY(-100%);
    transition: transform 0.3s ease-in-out;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
    padding-top: 60px;
    /* padding-left: 4%; */
    padding-bottom: 20px;
  }

  .navLinks > li {
    height: 18px;
    width: 100%;
  }
  .navLinks > li >a {
    display: block;
  }

  .navLinks > li >.active {
    border-radius: 0;
    }
  .navBranding {
    font-size: 22px;
  }

  .profile>img {
    height: 40px;
    width: 40px;
  }
  
  .searchBtn{
    margin-left: 10px;
    color: var(--clrdark);
  }
}

@media (width < 650px){
  .PanelBtnNProfile{
    margin-bottom: 10px;
  }
  .switch {
    position: relative;
    display: inline-block;
    width: 30px;
    height: 15px;
  }

  .darkModeDiv{
    margin-right: 0px;
  }

  .slider:before {
    position: absolute;
    content: "";
    height: 15px;
    width: 15px;
    background-color: var(--clrlighter);
    -webkit-transition: .4s;
    transition: .4s;
  }

  .darkmodeBtn:checked + .slider:before {
    -webkit-transform: translateX(16px);
    -ms-transform: translateX(26px);
    transform: translateX(16px);
  }

  .darkModeDiv .fa-sun {
    color: gold;
    font-size: 15px;
    margin-right: 8px;
    transition: 0.8s;
  }
  .darkModeDiv .fa-moon {
    color: var(--clrdark);
    font-size: 15px;
    transition: 0.8s;
    margin-left: 8px;
  }

}

@media (width < 520px){

  .hamburger{
    margin-top: 27px;
    cursor: default;
    margin-left: 10px;
  }
  .navBranding {
    font-size: 20px;
  }
  .profile>img {
    cursor: default;
    height: 36px;
    width: 36px;
  }

  .authBtnOuters > .btn{
    font-size: 12px;
  }

  .darkModeDiv{
    margin-right: 5px;
    width: 85px;
  }
  .navBtns > .Btn{
    display: none;
  }
  .navBtns{
    width: 35;
    justify-content: center;
  }
  .brandAndLinks{
    width: 30%;
  }

  .navBtns > .loginBtn{
    font-size: 15px;
    margin-right: 10px;
  }

}