.ContactUsBox {
    transition: top 1s cubic-bezier(0.175, 0.885, 0.32, 1.275);
    width: 750px;
    overflow: hidden;
    background-color: var(--clrbackground);
    padding: 10px 10px 40px 10px;
    position: relative;
    border-radius: 10px;
}

.contactUs{
    text-align: left;
    width: 80%;
}
.form > p{
    text-align: start;
    width: 80%;
    margin-bottom: 15px;
}
.form > h2{
    font-size: 25px;
}

.contactUsClose{
    width: 100%;
    float: right;
    font-size: 25px;
    margin-right: 20px;
}

.messageBox{
    background-color: var(--clrbackground);
}

@media (width < 1050px) {

    .form > p{
        text-align: center;
        width: 80%;
        margin-bottom: 15px;
    }
    .ContactUsBox {
        width: 80%;
    }
    .contactUs{
        display: flex;
        justify-content: center;
        width: 80%;
    }
    .contactUs{
        width: 100%;
    }
}

@media (width < 800px) {
    .ContactUsBox {
        padding: 30px 50px;
    }
.contactUsClose{
    margin-right: 0;
}
}
@media (width < 500px) {
    .ContactUsBox {
        width: 90%;
    padding: 10px 20px 40px 20px;

    }
}