.postPage {
    min-height: 80vh;
    display: flex;
    flex-direction: column;
}

.post_Title {
    width: 100%;
    background-color: rgb(77, 109, 117);
    min-height: 230px;
    /* height: 30px; */
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-start;
    border-bottom: 2px solid var(--clr);
    padding-top: 105px;
}

.sectionName {
    background-color: white;
    color: rgb(113, 143, 151);
    padding: 5px 10px;
    cursor: pointer;
    font-size: 14px;
    border-radius: 5px;
    letter-spacing: 0.3px;
    margin-top: 10px;
}

.post_Title h1 {
    font-weight: normal;
    margin: 12px 0;
    padding: 10px 0;
    color: white;
    width: 80%;
    text-align: center;
}

.postBody {
    background-color: var(--clrtemplatebg);
    width: 100%;
    padding-bottom: 30px;
    display: flex;
    justify-content: center;
}

.postContainer {
    width: 95%;
    max-width: 1520px;
    display: flex;
    justify-content: center;
}

.PostsAndSide {
    display: flex;
    margin-top: 40px;
    width: 95%;
    justify-content: space-between;
}

.posts {
    width: 92%;
    background-color: var(--clrtemplatebg);
    display: flex;
    border-radius: 10px;
    ;
    padding: 10px 30px 0 10px;
    flex-direction: column;
    align-items: flex-end;
    margin-right: 25px;
}

.notloggedinbox {
    display: flex;
    justify-content: center;
    margin: 0px 0 40px 0;
    width: 100%;
}

.notloggedinbox>div {
    border-radius: 5px;
    color: var(--clrdark);
    padding: 25px 45px;
}

.notloggedinbox>div>p {
    display: inline;
}

.btnBlue {
    background-color: var(--clrtemplatebg);
    margin: 0 20px;
    cursor: pointer;
    font-size: 17px;
    color: var(--clrdark);
    border: none;
}

.btnBlue:hover {
    text-decoration: underline;
}

.sideOptions {
    /* border-left: 1px solid var(--clrlighter); */
    width: 8%;
    display: flex;
    padding-top: 30px;
    /* align-items: flex-end; */
    /* background-color: green; */
    flex-direction: column;
    /* justify-content: flex-start; */
    position: relative;
    margin-bottom: 30px;
}

.postReply {
    background-color: var(--clrprimary);
    outline: none;
    border: none;
    color: white;
    padding: 12px 5px;
    opacity: 0.9;
    font-weight: bold;
    position: sticky;
    border-radius: 5px;
    transition: 0.2s;
    top: 100px;
    cursor: pointer;
    margin-bottom: 10px;
}

.postReply>span {
    color: white;
}

.postReply:nth-child(2) {
    top: 150px;

}

.postReply:nth-child(3) {
    top: 200px;
}

.postReply:nth-child(4) {
    top: 250px;
}

.postReply:hover {
    background-color: var(--clrprimarydark);
}

.postReply>i {
    color: white;
    margin-right: 5px;
    padding-top: 2px;
}

.RepliesBox {
    display: flex;
    flex-direction: column;
    width: 100%;
    /* padding: 0px 60px; */
    /* padding-right: 30px; */
}

.replytopostdiv {
    border-radius: 10px;
    padding: 20px 0;
    cursor: pointer;
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.replytopostdiv>.postUserImg {
    padding: 0;
    width: 60px;
    height: 60px;
    /* width: 6.5%; */
    display: flex;
    margin-left: 10px;
    justify-content: center;
}

.replytopostdiv>.postUserImg>img {
    /* margin: 0 50px; */
    width: 60px;
    margin: 0;
    height: 60px;
}

.replytopostdiv:hover {
    outline: 1px dashed var(--clrlight);
}

.WriteComment {
    width: 92%;
    padding: 20px 20px;
    font-size: 18px;
    color: var(--clrlight);
}

.commentBox {
    display: flex;
    justify-content: center;
    position: sticky;
    bottom: 0;
}

.commentBox>* {
    max-width: 1520px;
    width: 90%;
}


@media (width < 1050px) {



    .replytopostdiv>.postUserImg > img {
        height: 50px;
        width: 50px;
        margin-left: 10px;
    }
    .postReply {
        border-radius: 50px;
        width: 40px;
        height: 40px;
        margin-right: 10px;
        display: flex;
        justify-content: center;
        align-items: center;
    }

    .postContainer {
        width: 95%;
    }

    .postBtnName {
        background-color: white;
        display: none;
    }

    .postReply>i {
        margin-right: 0;
    }

    .sideOptions {
        width: 1%;
        margin-right: 10px;
        margin-left: 5px;
        margin-bottom: 0;
    }

    .posts {
        width: 100%;
        padding: 0;
        margin-right: 0;
        align-items: flex-end;
    }

    .PostsAndSide {
        justify-content: center;
    }

    .post_Title h1 {
        font-size: 26px;
    }

    .replytopostdiv {
        padding: 0;
        margin-right: 20px;
    }

    .replytopostdiv>.postUserImg {
        margin-right: 15px;
    }
}

@media (width < 750px) {

    .notloggedinbox,
    .notloggedinbox>div>button {
        font-size: 13px;
    }
    /* .notloggedinbox>div>p {
        display: block;
        text-align: center;
        margin-bottom: 10px;
        background-color: red;
    } */
    
}

@media (width < 650px) {
    .notloggedinbox>div>p {
        display: block;
        text-align: center;
        margin-bottom: 10px;
    }
    .replytopostdiv>.postUserImg > img {
        height: 40px;
        width: 40px;
    }
    .postBody{
        justify-content: flex-start;
    }
    .sideOptions .postReply{
        width: 30px;
        height: 30px;
    }
}

@media (width < 420px) {
    .btnBlue{
        margin: 0 11px;
    }
}
@media (width < 600px) {
    .postReply {
        border-radius: 50px;
        width: 35px;
        height: 35px;
        /* margin-right: 10px; */
    }
    .postContainer{
        width: 97%;
    }

    .post_Title h1 {
        font-size: 22px;
    }
}

