.EditorBox {
    border-left: 3px solid var(--clrprimary);
    border-top: 3px solid var(--clrprimary);
    border-right: 3px solid var(--clrprimary);
    padding: 2px;
    min-height: 400px;
    border-radius: 10px 10px 0 0;
    background-color: var(--clrbackground);
    width: 100%;
    display: flex;
    z-index: 1000;
    animation: fadein 0.2s;
    position: sticky;
    bottom: 0;
}

.EditorBox:nth-child(2) {
    display: flex;
    flex-direction: column;
}

.EditorBox>.postUserImg {
    align-items: center;
    padding-top: 30px;
    margin-left: 10px;
}
.EditorBox>.postUserImg > img{
    border-radius: 50px;
}
.EditorBox>.postUserImg>img {
    width: 70px;
    height: 70px;
}

.EditorContent {
    width: 100%;
    padding: 10px;
    height: 300px;
    /* overflow: scroll; */
}

.editorTitleBox {
    width: 100%;
    outline: 1px solid var(--clrdivseperatebar);
    border: none;
    background-color: var(--clrdivcolor);
    margin: 14px 0px 17px 0;
    border-radius: 10px;
    font-size: 18px;
    padding: 5px 10px;
    color: var(--clrdark);

}

.editorTitleBox:focus {
    border: none;
}

.jodit-status-bar {
    display: none;
}

.EditorBox>.CloseBtn {
    position: absolute;
    padding-top: 12px;
    height: 15px;
    cursor: pointer;
    color: var(--clrdark);
    padding: 0 20px;
    /* width: 100%; */
    text-align: end;
    font-size: 25px;
    right: 0;
}

.EditorBox>.CloseBtn:hover {
    color: var(--clrprimary);
}

.categNTitle {
    display: flex;
    flex-direction: column;
    width: 97%;
}

.categNTitle>select {
    outline: 1px solid var(--clrdivseperatebar);
    height: 30px;
    margin-top: 15px;
    margin-right: 10px;
    border-radius: 10px;
    width: 100%;
    padding: 0 15px 0 10px;
    margin-top: 25px;
    border: none;
    color: var(--clrprimary);
    background-color: var(--clrdivcolor);
}

.categNTitle>select:focus {
    border: none;
}

.joditBox {
    overflow: auto;
    width: 97%;
    max-height: 200px;
    /* max-height: 250px; */
}


.postBtn>button {
    margin-top: 15px;
    padding-left: 25px;
    padding-right: 25px;
}

.postBtn>button>i {
    color: white;
    margin-right: 5px;
}


.jodit-wysiwyg {
    width: 100%;
    background-color: var(--clrdivcolor);
}
.jodit-toolbar__box:not(:empty){
    border-bottom: 2px solid var(--clrbackground);
}
.jodit-toolbar__box:not(:empty):not(:empty) {
    background-color: var(--clrdivcolor);
}
.jodit-wysiwyg > p{
    color: var(--clrdark);
}
.jodit-container:not(.jodit_inline){
    border: 1px solid var(--clrtemplatebg);
    
}
.jodit-ui-spacer{
    background-color: var(--clrdivcolor);
    border-left: 1px solid var(--clrtemplatebg);
    border-right: 1px solid var(--clrtemplatebg);
}
.jodit-toolbar-button__icon > svg{
    color: red;
}
.jodit-toolbar-editor-collection_size_middle.jodit-toolbar-editor-collection_mode_horizontal {
    background-image: -o-repeating-linear-gradient(transparent 0,transparent 37px,var(--clrbackground) 38px);
    background-image: repeating-linear-gradient(transparent 0,transparent 37px,var(--clrbackground) 38px);
}
.jodit-toolbar__box:not(:empty) {
    border-radius: 0;
}
.jodit-toolbar__box:not(:empty) .jodit-toolbar-editor-collection:after {
    background-color: var(--clrbackground);
}
.jodit-ui-group_separated_true:not(:last-child):not(.jodit-ui-group_before-spacer_true):after {
    border-right: 1px solid var(--clrbackground);
}
.jodit-ui-separator {
    border-right: 1px solid var(--clrtemplatebg);
}
@media (width < 950px) {
    .EditorBox>.postUserImg>img {
        display: none;
        width: 0;
        height: 0;
    }
    .categNTitle{
        width: 100%;
    }
    .joditBox{
        width: 100%;
    }
}